<template>
  <div class="apanage_add">
    <div class="box first arrow">
      <label class="row input_row" for="break_type">
        <div class="left">
          检查岗位:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.post}">
          <span>{{showPostCheck}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <select name="post" id="break_type" v-model="formData.post">
            <option value=""></option>
            <option value="guard">保安</option>
            <option value="check">安检</option>
            <option value="clean">保洁</option>
          </select>
        </div>
      </label>
      <div class="hr"></div>
      <label class="row input_row" for="basis">
        <div class="left">
          参考依据:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.basis}">
          <span>{{this.formData.basis ? this.formData.basis: '请输入参考依据'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <input type="text" v-model="formData.basis" id="basis">
        </div>
      </label>
      <div class="hr"></div>
      <label class="row input_row" for="score">
        <div class="left">
          考核分值:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.score}">
          <span>{{this.formData.score ? this.formData.score: '请输入考核分值'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <input type="number" v-model="formData.score" id="score">
        </div>
      </label>
      <div class="hr"></div>
      <label class="row input_row" for="deviceLine">
        <div class="left">
          所在线路:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.deviceLine}">
          <span>{{this.formData.deviceLine ? this.formData.deviceLine.name: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <select name="deviceLine" id="deviceLine" v-model="formData.deviceLine">
            <option value=""></option>
            <option :value="item" v-for="item in lines" :key="item.id">{{item.name}}</option>
          </select>
        </div>
      </label>
      <div class="hr"></div>
      <label class="row input_row" for="devicePosition">
        <div class="left">
          所在位置:
        </div>
        <div class="right" :class="{'placeholder': !this.formData.devicePosition}">
          <span>{{this.formData.devicePosition ? this.formData.devicePosition.name: '请选择'}}</span>
          <img src="@/assets/imgs/qianjin.png" alt="">
        </div>
        <div class="input_box">
          <select name="devicePosition" id="devicePosition" v-model="formData.devicePosition">
            <option value=""></option>
            <option :value="item" v-for="item in stations" :key="item.id">{{item.name}}</option>
          </select>
        </div>
      </label>
    </div>
    <div class="box second">
      <div class="row">
        检查情况:
      </div>
      <div class="hr"></div>
      <div class="row textarea_box">
        <textarea name="" id="desc" rows="5" v-model="formData.desc" placeholder="请输入检查情况"></textarea>
      </div>
    </div>
    <div class="box second">
      <div class="row">
        发现问题:
      </div>
      <div class="hr"></div>
      <div class="row textarea_box">
        <textarea name="" id="" rows="5" v-model="formData.question" placeholder="请输入问题"></textarea>
      </div>
    </div>
    <div class="box second">
      <div class="row">
        备注:
      </div>
      <div class="hr"></div>
      <div class="row textarea_box">
        <textarea name="" id="" rows="5" v-model="formData.mark" placeholder="请输入备注"></textarea>
      </div>
    </div>
    <div class="box third">
      <div class="row">
        照片:
      </div>
      <div class="hr"></div>
      <div class="row img_box">
        <upload :max="9" :handleClick="true" size="small" :token="imgUpToken" uploadUrl="http://up-z1.qiniu.com" :images="this.formData.imgs" @add-image="selectImg" @remove-image="rmImg"/>
      </div>
    </div>
    <common-btn class="add_btn" @click="commitChange">确认提交</common-btn>
    <div class="blank"></div>
  </div>
</template>

<script>
export default {
  name: 'ApanageAdd',
  data () {
    return {
      imgUpToken: 'cJqhgC9wZVkfNJEKq73x5ZCKsnLzIeEOKbVLvWD7:axqBqV5JMVpDHEhCiwEZNI03KLA=:eyJzY29wZSI6IndkYXRhIiwiZGVhZGxpbmUiOjE1NDMzNzU3MjJ9',
      lines: [],
      stations: [],
      formData: {
        post: '',
        basis: '',
        score: '',
        deviceLine: null,
        devicePosition: null,
        desc: '',
        question: '',
        mark: '',
        imgs: []
      }
    }
  },
  computed: {
    showPostCheck () {
      switch (this.formData.post) {
        case 'guard':
          return '保安'
        case 'check':
          return '安检'
        case 'clean':
          return '保洁'
        default:
          return '请选择'
      }
    }
  },
  methods: {
    selectImg () {
      let that = this
      // eslint-disable-next-line
      dd.biz.util.uploadImage({
        compression: false,
        quality: 100,
        resize: 100,
        stickers: {
          time: new Date()
        },
        onSuccess: function (result) {
          that.formData.imgs.push({ url: result[0] })
        },
        onFail: function (err) {
          alert(JSON.stringify(err))
        }
      })
    },
    rmImg () {
      this.formData.imgs.splice(0, 1)
    },
    getLine () {
      this.$rpc.call('cdtct_dingtalk.cdtct_dingtalk_department', 'get_line_id', [], {}).then(res => {
        this.lines = res
      }).catch(e => {
        console.log(e)
        alert('获取线路失败')
      })
    },
    getSites (id) {
      this.$rpc.call('cdtct_dingtalk.cdtct_dingtalk_department', 'get_sites', [], {
        line_id: id
      }).then(res => {
        this.stations = res
      }).catch(e => {
        console.log(e)
        alert('获取地址失败')
      })
    },
    commitChange () {
      if(!this.formData.deviceLine || !this.formData.deviceLine.id){
        alert('请先选择线路')
      }else if (!this.formData.devicePosition || !this.formData.devicePosition.id){
        alert('请先选择站点')
      }else{
        this.$rpc.call('funenc_xa_station.belong_to_management', 'save_belong_to_management', [], {
          vals: {
            post_check: this.formData.post,
            reference_according: this.formData.basis,
            check_score: parseInt(this.formData.score),
            line_id: this.formData.deviceLine && this.formData.deviceLine.id,
            site_id: this.formData.devicePosition && this.formData.devicePosition.id,
            find_problem: this.formData.question,
            check_state: this.formData.desc,
            note: this.formData.mark,
            imgs: JSON.stringify(this.formData.imgs)
          }
        }).then(res => {
          if (res) {
            this.$router.push({
              path: '/apanage'
            })
          }
        }).catch(e => {
          console.log(e)
          alert('提交失败')
        })
      }
    }
  },
  mounted () {
    this.getLine()
  },
  watch: {
    'formData.deviceLine': {
      handler (v) {
        this.getSites(v.id)
      }
    }
  }
}
</script>

